import React, { useState, useEffect } from "react";
import {
  Container,
  Typography,
  Divider,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  Snackbar,
  SnackbarContent,
  List,
  ListItem,
  ListItemText,
} from "@material-ui/core";
import { connect } from "react-redux";
import moment from "moment";
import { withRouter } from "react-router-dom";
import { ArrowBack, Close, DeleteOutline } from "@material-ui/icons";
import { red, green } from "@material-ui/core/colors";
import api from "../../api/api";
import { logout } from "../../actions";

const Query = (props) => {
  const { logout, update, history, remove, query } = props;

  const [opendialog, setopendialog] = useState(false);
  const [toast, settoast] = useState(false);
  const [message, setmessage] = useState(null);
  const [status, setstatus] = useState(null);
  const [values, setValues] = useState(null);

  useEffect(() => {
    document.title = "Query";
  }, []);

  const deleteQuery = async () => {
    if (localStorage.getItem("mom-admin")) {
      try {
        await api.delete(
          `/api/query/${values.id}`,

          {
            headers: {
              Authorization:
                "Bearer " + localStorage.getItem("mom-admin"),
            },
          }
        );
        remove(values.id);
        setValues([]);
        setopendialog(false);
        setstatus(green[700]);
        setmessage("Query Deleted Successfully");

        return settoast(true);
      } catch (err) {
        setstatus(red[700]);
        setmessage("Something went wrong");
        return settoast(true);
      }
    } else {
      return logout();
    }
  };

  const setRead = async (q) => {
    if (localStorage.getItem("mom-admin")) {
      try {
        await api.patch(
          `/api/query/${q.id}`,
          {
            read: true,
          },
          {
            headers: {
              Authorization:
                "Bearer " + localStorage.getItem("mom-admin"),
            },
          }
        );

        return update({ ...q, read: true });
      } catch (err) {
        return;
      }
    } else {
      return logout();
    }
  };

  const renderList = () => {
    if (query.length) {
      return (
        <List>
          {query.map((a, i) => {
            return (
              <ListItem
                key={i}
                button
                style={{
                  borderBottom: "1px solid #ddd",
                  backgroundColor: a.read ? "#fff" : "#eff2f1",
                }}
                onClick={() => {
                  setValues(a);
                  if (!a.read) setRead(a);
                  setopendialog(true);
                }}
              >
                <ListItemText
                  primary={
                    <Typography style={{ fontWeight: 600 }}>
                      {a.name}
                    </Typography>
                  }
                  secondary={
                    <React.Fragment>
                      <Typography
                        color="textSecondary"
                        style={{
                          marginTop: 2,
                          marginBottom: 5,
                          fontSize: 12,
                        }}
                      >
                        {moment(a.created).format("MMMM Do YYYY, h:mm A")}
                      </Typography>
                      <Typography
                        color="textPrimary"
                        style={{
                          whiteSpace: "nowrap",
                          textOverflow: "ellipsis",
                          width: "100%",
                          overflow: "hidden",
                        }}
                      >
                        {a.subject ? <b>{a.subject}: </b> : null}{a.message}
                      </Typography>
                    </React.Fragment>
                  }
                />
              </ListItem>
            );
          })}
        </List>
      );
    } else {
      return (
        <Typography align="center" variant="h6" style={{ marginTop: 300 }}>
          NO DATA TO DISPLAY!
        </Typography>
      );
    }
  };

  const renderdialog = () => {
    if (values)
      return (
        <Dialog
          scroll="body"
          open={opendialog}
          onClose={() => {
            setValues(null);
            setopendialog(false);
          }}
        >
          <DialogTitle>
            <div style={{ display: "flex" }}>
              Query
              <div
                style={{
                  display: "flex",
                  flexGrow: 1,
                  justifyContent: "flex-end",
                }}
              >
                <IconButton
                  edge="end"
                  size="small"
                  onClick={() => {
                    if (
                      window.confirm(
                        `Are you sure you want to delete this query? You cannot recover once deleted!`
                      )
                    ) {
                      return deleteQuery();
                    }
                  }}
                >
                  <DeleteOutline color="primary" />
                </IconButton>
                <IconButton
                  edge="end"
                  size="small"
                  onClick={() => {
                    setValues(null);
                    setopendialog(false);
                  }}
                >
                  <Close color="primary" />
                </IconButton>
              </div>
            </div>
          </DialogTitle>
          <DialogContent dividers>
            <Typography
              color="textSecondary"
              style={{ fontSize: 10, marginTop: 8 }}
            >
              NAME
            </Typography>
            <Typography style={{ fontWeight: "bold" }}>
              {values.name}
            </Typography>
            <Typography
              color="textSecondary"
              style={{ fontSize: 10, marginTop: 8 }}
            >
              PHONE NUMBER
            </Typography>
            <Typography style={{ fontWeight: "bold" }}>
              {values.phone}
            </Typography>
            <Typography
              color="textSecondary"
              style={{ fontSize: 10, marginTop: 8 }}
            >
              EMAIL ADDRESS
            </Typography>
            <Typography style={{ fontWeight: "bold" }}>
              {values.email}
            </Typography>
            <Typography
              color="textSecondary"
              style={{ fontSize: 10, marginTop: 8 }}
            >
              ADDRESS
            </Typography>
            <Typography style={{ fontWeight: "bold" }}>
              {values.address}
            </Typography>
            <Typography
              color="textSecondary"
              style={{ fontSize: 10, marginTop: 8 }}
            >
              DATE
            </Typography>
            <Typography color="textSecondary">
              {moment(values.created).format("MMMM Do YYYY, h:mm A")}
            </Typography>
            {
              values.subject &&
              (
                <>
                  <Typography
                    color="textSecondary"
                    style={{ fontSize: 10, marginTop: 8 }}
                  >
                    SUBJECT
            </Typography>
                  <Typography style={{ fontWeight: "bold" }}>
                    {values.subject}
                  </Typography>
                </>
              )
            }

            <Typography
              color="textSecondary"
              style={{ fontSize: 10, marginTop: 8 }}
            >
              MESSAGE
            </Typography>
            <Typography style={{ whiteSpace: "pre-line" }}>
              {values.message}
            </Typography>
          </DialogContent>
        </Dialog>
      );
  };

  return (
    <Container>
      <Typography variant="h6">
        <IconButton
          edge="start"
          onClick={() => {
            history.goBack();
          }}
        >
          <ArrowBack />
        </IconButton>
        Query (Zawhna)
      </Typography>
      <Divider style={{ marginTop: 20 }} />
      {renderList()}
      {renderdialog()}

      <Snackbar
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
        open={toast}
        autoHideDuration={3000}
        onClose={() => {
          settoast(false);
        }}
      >
        <SnackbarContent
          style={{ backgroundColor: status }}
          message={message}
          onClose={() => {
            settoast(false);
          }}
          action={
            <IconButton
              key="close"
              color="inherit"
              onClick={() => settoast(false)}
            >
              <Close />
            </IconButton>
          }
        ></SnackbarContent>
      </Snackbar>
    </Container>
  );
};

const mapstatetoprops = (state) => {
  return { query: state.query };
};

const mapdispatchtoprops = (dispatch) => ({
  update: (obj) => {
    dispatch({
      type: "UPDATE_QUERY",
      payload: obj,
    });
  },
  remove: (id) => {
    dispatch({
      type: "REMOVE_QUERY",
      payload: id,
    });
  },

  logout: () => dispatch(logout()),
});

export default connect(mapstatetoprops, mapdispatchtoprops)(withRouter(Query));
